<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
      <div class="fixed-box">
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <nav-head :title="title" ></nav-head>
      </div>
      <!--  搜索框-->
      <div class="search-box">
          <van-search
            v-model="value"
            shape="round"
            background="#202641"
            placeholder="请输入您要查找的币种地址"
          />
      </div>
      </div>
      <!-- 固定区域************************ -->
      <!-- 占位区域 -->
      <div class="zhanwei"></div>
      <div class="item-box">
      <!-- list  循环区域 -->
      <div class="item" v-for="(item,index) in withdrawlistList" :key="index">
          <div class="item-left">
          <van-checkbox @click="butCheck(index)" v-model="item.checked" checked-color="#54D25C"></van-checkbox>
          </div>
          <div class="item-right">
              <div class="row-1 row-g">
                  <!-- <img src="../../assets/image/capital/btb.png" alt="" style="vertical-align: middle;"> margin-left: 0.24rem-->
                  <span style="font-size: 0.28rem;">{{item.remarks}}充值地址</span>
              </div>
              <div class="row-2 row-g">{{item.address}}</div>
              <!-- <div class="row-3 row-g">添加时间：2020.06.04 15.47</div> -->
          </div>
      </div>
      </div>
      <!-- 删除地址  btn -->
      <div class="del-btn" @click="delAddress()">
          删除地址
      </div>

    </div>
  </div>
</template>

<script>
// withdrawlist
import Vue from 'vue'
import { withdrawlist, delwithdraw } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { Search, Tab, Tabs, Checkbox, CheckboxGroup, Toast } from 'vant'
import NavHead from '@/components/head.vue'
Vue.use(Checkbox).use(CheckboxGroup).use(Search).use(Tab).use(Tabs).use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      checked: true,
      active: 0,
      radio: '1',
      value: '',
      withdrawlistList: [],
      curHeight: '',
      withdrawlistData: {
        u_id: 1
      },
      delwithdrawData: {
        u_id: 1,
        address_id: ''
      },
      checkedIndex: '', // 选中的下标
      title: '提币地址管理'
    }
  },
  created () {
    this.withdrawlistData.u_id = sessionStorage.getItem('u_id')
    this.delwithdrawData.u_id = sessionStorage.getItem('u_id')
    console.log(sessionStorage.getItem('u_id'), 'u_id')
    this.beforeMount()
    this.withdrawFun()
  },
  methods: {
    delAddress () {
      this.delwithdrawFun()
    },
    butCheck (index) {
      var that = this
      for (const [i, v] of that.withdrawlistList.entries()) {
        if (i === index) {
          if (v.checked === true) {
            that.delwithdrawData.address_id = v.id
            that.checkedIndex = index
          } else {
            that.delwithdrawData.address_id = ''
            that.checkedIndex = ''
          }
        } else {
          this.withdrawlistList[i].checked = false
        }
      }
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    delwithdrawFun () {
      var that = this
      if (that.delwithdrawData.address_id === '') {
        that.$toast({ message: '选择地址不能为空', duration: 1500 })
        return
      }
      var data = objKeySort(this.delwithdrawData)
      var token = sgin(data)
      this.delwithdrawData.token = token
      delwithdraw(that.delwithdrawData).then(res => {
        if (res.code === 0) {
          that.$toast({
            message: '删除成功',
            duration: 1500
          })
          that.withdrawlistList.splice(that.checkedIndex, 1)
          delete that.delwithdrawData.token
        }
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        delete that.delwithdrawData.token
      })
    },
    // 提现地址列表
    withdrawFun () {
      var that = this
      var data = objKeySort(this.withdrawlistData)
      var token = sgin(data)
      this.withdrawlistData.token = token
      withdrawlist(that.withdrawlistData).then(res => {
        that.withdrawlistList = res.data
        var arry = res.data
        var arry2 = []
        arry.map((item, index) => {
          arry2.push(Object.assign({}, item, { checked: false }))
        })
        that.withdrawlistList = arry2
        delete that.withdrawlistData.token
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete that.withdrawlistData.token
      })
    }
  }
}
</script>
